import React, {useState, useContext} from 'react'
import { styled, Box } from '@mui/system';
import ModalUnstyled from '@mui/base/ModalUnstyled';
import { Close } from '@styled-icons/evaicons-solid';
import Info1 from '../tools/info/editTutorial/1.png'
import Info2 from '../tools/info/editTutorial/2.png'
import Info3 from '../tools/info/editTutorial/3.png'
import Info4 from '../tools/info/editTutorial/4.png'
import Info5 from '../tools/info/editTutorial/5.png'
import Info6 from '../tools/info/editTutorial/6.png'
import Info7 from '../tools/info/editTutorial/7.png'

const StyledModal = styled(ModalUnstyled)`
  position: fixed;
  z-index: 1300;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Backdrop = styled('div')`
  z-index: -1;
  position: fixed;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-tap-highlight-color: transparent;
`;

const style = {
  width: 700,
  height: 500,
  bgcolor: 'rgba(255,255,255,1)',
  borderRadius:2,
  p: 2,
  px: 4,
  pb: 3,
};
const Desc = styled('div')`
    margin-top:12px;
    font-size:14px;
    text-align:center;
`;

function MakeTutorialModal({open, setOpen}) {
    const [page, setPage] = useState(1)

    const toPrev = () => {
        if(page>1)
            setPage(page-1)
        else
            return
    }

    const toNext = () => {
        if(page<4)
            setPage(page+1)
        else return
    }

    const returnTutorials = () => {
        switch(page){
            case 1: return ( 
                <div>
                    <div className="sub-text">
                        ! Surfee  .
                    </div>
                    <div className="card-container">
                        <div className="card">
                            <img className="card-img" src={Info1} alt="1" />
                            <Desc>     ,</Desc>
                        </div>
                        <div className="card">
                            <img className="card-img" src={Info2} alt="2" />
                            <Desc>      .</Desc>
                        </div>
                    </div>
                </div>)

            case 2: return ( <div>
                <div className="sub-text" />
                    <div className="card-container">
                        <div className="card">
                            <img className="card-img" src={Info3} alt="3" />
                            <Desc>     ,</Desc>
                        </div>
                        <div className="card">
                            <img className="card-img" src={Info4} alt="4" />
                            <Desc>       .</Desc>
                        </div>
                    </div>
                </div>)

            case 3: return ( <div>
                <div className="sub-text" />
                    <div className="card-container">
                        <div className="card">
                            <img className="card-img" src={Info5} alt="5" />
                            <Desc>     </Desc>
                        </div>
                        <div className="card">
                            <img className="card-img" src={Info6} alt="6" />
                            <Desc>      .</Desc>
                        </div>
                    </div>
                </div>)

            case 4: return ( <div>
                    <div className="card-container">
                        <div className="card">
                            <img className="card-img" src={Info7} alt="7" />
                            <Desc>          .</Desc>
                        </div>
                    </div>
                <div className="sub-text">
                Surfee  3      !
                </div>
                </div>)

            default:
                return(
                    <div>
    
                    </div>
                )
        }
    }

    return (
        <div>
            <StyledModal
                aria-labelledby="unstyled-modal-title"
                aria-describedby="unstyled-modal-description"
                open={open}
                onClose={() => setOpen(!open)}
                BackdropComponent={Backdrop}
            >
                <Box sx={style}>
                    <div className="modal-top__title">
                        <div className="centera">
                            <div className="modal-text-title">
                                 
                            </div>
                        </div>
                        <div className="modal-close-button" onClick={() => setOpen(false)}>
                            <Close size="30" />
                        </div>
                    </div>
                    <div className="tutorial-box">
                        {returnTutorials()}
                    </div>
                    <div className="prev-next-buttons">
                        {
                            page === 4 ? 
                            <>
                                <div className="modal-move-button-back uphover" onClick={e => setPage(page - 1)} style={{visibility:`${page === 1 ? 'hidden' : 'visible'}`}}></div>
                                <div className="modal-move-button uphover" onClick={() => {setOpen(false); setPage(1)}}> </div>
                            </>
                            :
                            <>
                                <div className="modal-move-button-back uphover" onClick={e => setPage(page - 1)} style={{visibility:`${page === 1 ? 'hidden' : 'visible'}`}}></div>
                                <div className="modal-move-button uphover" onClick={e => setPage(page + 1)}></div>  
                            </>
                        }
                    </div>
                </Box>
            </StyledModal>
        </div>
    )
}

export default MakeTutorialModal
