import React, {useState, useContext} from 'react'
import { styled, Box } from '@mui/system';
import ModalUnstyled from '@mui/base/ModalUnstyled';
import { Close } from '@styled-icons/evaicons-solid';
import rt1 from './img/tutorial/rt1.png'
import rt2 from './img/tutorial/rt2.png'
import rt3 from './img/tutorial/rt3.png'
import rt4 from './img/tutorial/rt4.png'

const StyledModal = styled(ModalUnstyled)`
  position: fixed;
  z-index: 1300;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Backdrop = styled('div')`
  z-index: -1;
  position: fixed;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-tap-highlight-color: transparent;
`;

const style = {
  width: 600,
  height: 550,
  bgcolor: 'rgba(255,255,255,1)',
  borderRadius:2,
  p: 2,
  px: 4,
  pb: 3,
};
const Desc = styled('div')`
    margin-top:12px;
    font-size:14px;
    text-align:center;
`;

const Bold = styled('span')`
    color:var(--main-color);
`

function MakeTutorialModal({open, setOpen}) {
    const [page, setPage] = useState(1)

    const ModalBody = (props) => {
        return(
            <div className="center-row">
                <div className="center-column" style={{fontSize:'16px'}}>
                    {props.children}
                </div>
            </div>
        )
    }

    const returnTutorials = () => {
        switch(page){
            case 1: return ( 
                <ModalBody>
                    <div style={{textAlign:'center', margin:'12px 0px'}}>
                        !<br/>
                     <Bold>   </Bold>     <br/>
                    <Bold> </Bold>  .
                    </div>
                    <img src={rt1} style={{width:'60%', border:'1px solid var(--main-light-gray-02', boxShadow:'2px 4px 20px #E8F0F9'}}/>
                    <Desc>
                        ,        .
                    </Desc>
                </ModalBody>
            )

            case 2: return ( 
                <ModalBody>
                    <img src={rt2} style={{width:'70%', border:'1px solid var(--main-light-gray-02', boxShadow:'2px 4px 20px #E8F0F9'}}/>
                    <Desc>
                            ''   <br/>
                            .
                    </Desc>
                </ModalBody>)

            case 3: return ( 
                <ModalBody>
                    <img src={rt3} style={{width:'70%', border:'1px solid var(--main-light-gray-02', boxShadow:'2px 4px 20px #E8F0F9'}}/>
                    <Desc>
                           <br/>
                             30  .<br/>
                                .
                    </Desc>
                </ModalBody>)

            case 4: return ( 
                <ModalBody>
                    <img src={rt4} style={{width:'70%', border:'1px solid var(--main-light-gray-02', boxShadow:'2px 4px 20px #E8F0F9'}}/>
                    <Desc>
                           3    . <br/>
                               ,<br/>
                           !
                    </Desc>
                </ModalBody>)

            default:
                return(
                    <div>
    
                    </div>
                )
        }
    }

    return (
        <div>
            <StyledModal
                aria-labelledby="unstyled-modal-title"
                aria-describedby="unstyled-modal-description"
                open={open}
                onClose={() => setOpen(!open)}
                BackdropComponent={Backdrop}
            >
                <Box sx={style}>
                    <div className="modal-top__title">
                        <div className="centera">
                            Surfee 
                        </div>
                        <div className="modal-close-button" onClick={() => setOpen(false)}>
                            <Close size="30" />
                        </div>
                    </div>
                    <div className="centera" style={{height:'70%'}}>
                        {returnTutorials()}
                    </div>
                    <div className="modal-button-container">
                        {
                            page === 4 ? 
                            <>
                                <div className="modal-move-button-back uphover" onClick={e => setPage(page - 1)} style={{visibility:`${page === 1 ? 'hidden' : 'visible'}`}}></div>
                                <div className="modal-move-button uphover" onClick={() => {setOpen(false); setPage(1)}}>Surfee </div>
                            </>
                            :
                            <>
                                <div className="modal-move-button-back uphover" onClick={e => setPage(page - 1)} style={{visibility:`${page === 1 ? 'hidden' : 'visible'}`}}></div>
                                <div className="modal-move-button uphover" onClick={e => setPage(page + 1)}></div>  
                            </>
                        }
                    </div>
                </Box>
            </StyledModal>
        </div>
    )
}

export default MakeTutorialModal
