import React,{useRef} from 'react'

function FuncFaviconImg({text, value, func, subtext}) {
    const photoInput = useRef();
    const inputClick = () => {
        photoInput.current.click();
    };

    return (
        <div className="edit-element">
        <div className="top-img-div">
        <div className="put-img-div" 
            onClick={inputClick}
            onChange={e => func(e)}
            id='attach'
            style={{backgroundImage: `url(${value}`}}
            >
            {/* <div 
                className="img-remove"
                onClick={ e => removeFunc(e) }
            >X</div> */}
            <input
                type="file" 
                accept="image/*" 
                id="file" 
                onChange={ e => func(e) }
                ref={photoInput}
                style={{display: 'none', cursor: 'pointer', objectFit:'cover'}}
            /> 
            {/* <div className="img-command">
            5MB , <br /> 1200px !
            </div> */}
        </div>
        <div className="upload-div">
            <div className="upload-img-click"
            onClick={inputClick}
            onChange={e => func(e)}
            id='attach'
            >
                {
                    value ? `${text} ` : `${text} `
                }
            </div>
        <div className="small-command">
        {subtext}
        </div>
        </div>
        </div>
        </div>
    )
}

export default FuncFaviconImg
