import React, { useContext } from 'react'
import { MyContext } from '../../../../../pages/Make/MakePageV2'
import produce from 'immer'
import OpenCloseCustom from '../../tools/Custom/OpenCloseCustom'
import ColorCustom from '../../tools/Custom/ColorCustom'
import TextSizeCustom from '../../tools/func/TextSizeCustom'
import RadioCustom from '../../tools/Custom/RadioCustom'

const alignOptions = [
    { label: '', value: 'start' },
    { label: '', value: 'center' },
]

const sizeOptions = [
    { label: '', value: 14 },
    { label: '', value: 16 },
    { label: '', value: 18 },
]

function EditNotice({content}) {
    const {state, action} = useContext(MyContext) //ContextAPI state action .

    return (
        <OpenCloseCustom title="" use={content.caution.use}>
           <>
            <ColorCustom text="" value={content.caution.color} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].caution.color = e;
            }))} />
            <RadioCustom text="" options={alignOptions} value={content.caution.align} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].caution.align = e;
            }))} />
            <TextSizeCustom text="" desc options={sizeOptions} value={content.caution.size} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].caution.size = e;
            }))} />
            </>
        </OpenCloseCustom>
    )
}

export default EditNotice
