import React, {useState, useContext} from 'react'
import { MyContext } from '../../../../pages/Make/MakePageV2'
import ElementsTable from './tools/ElementsTable'
import RadioCustom from '../tools/Custom/RadioCustom'
import produce from 'immer';
import EditTitleDesc from './tools/EditTitleDesc'
import SingleColorCustom from '../tools/Custom/SingleColorCustom'
import ColorCustom from '../tools/Custom/ColorCustom'
import OnOffCustom from '../tools/Custom/OnOffCustom'
import TextSizeCustom from '../tools/func/TextSizeCustom'
import OpenCloseCustom from '../tools/Custom/OpenCloseCustom'
import EditDesign from './tools/EditDesign'
import LayoutRFG from './tools/LayoutRFG'

const featureOptions = [
    { label: '', value: 'icon' },
    { label: '', value: 'image'},
]
const shapeOptions = [
    { label: '', value: 0 },
    { label: '', value: 5 },
    { label: '', value: 500 },
]
const imageSizeOptions = [
    { label: '', value: 60 },
    { label: '', value: 75 },
    { label: '', value: 100 },
]
const alignOptions = [
    { label: '', value: 'start'},
    { label: '', value: 'center'}
]


export const EditImageIcon = ({content}) => {
    const {state, action} = useContext(MyContext) //ContextAPI state action .

    return(
        <OpenCloseCustom title="/" use={content.element.use} tooltip="        .">
            <RadioCustom text="" value={content.element.type} options={featureOptions} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].element.type = e;
            }))} />
            {
                content.element.type === 'icon' && 
                <>
                <ColorCustom text="" value={content.element.backgroundColor} func={e => action.setContents(produce(state.contents, draft => {
                    draft[state.secNum].element.backgroundColor = e;
                }))} />
                <RadioCustom text="" button value={content.element.iconBorder} options={shapeOptions} func={e => action.setContents(produce(state.contents, draft => {
                    draft[state.secNum].element.iconBorder = e;
                }))} />
                <RadioCustom text="" value={content.element.size} options={imageSizeOptions} func={e => action.setContents(produce(state.contents, draft => {
                    draft[state.secNum].element.size = e;
                }))} />
                </>
            }
            {
                content.element.type === 'image' &&
                <>
                <RadioCustom text="" button value={content.element.imageBorder} options={shapeOptions} func={e => action.setContents(produce(state.contents, draft => {
                    draft[state.secNum].element.imageBorder = e;
                }))} />
                <RadioCustom text="" value={content.element.size} options={imageSizeOptions} func={e => action.setContents(produce(state.contents, draft => {
                    draft[state.secNum].element.size = e;
                }))} />
                </>
            }
        </OpenCloseCustom>
    )
}

function EditFeaturesSection({content, category}) {
    const {state, action} = useContext(MyContext) //ContextAPI state action .

    const elements = [
        {
            title:'',
            use:content.title.use,
            func:() => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].title.use = !content.title.use;
            }))
        },
        {
            title:'',
            use:content.desc.use,
            func:() => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].desc.use = !content.desc.use;
            }))
        },
        {
            title:'/',
            use:content.element.use,
            func:() => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].element.use = !content.element.use;
            }))
        },
        {
            title:'',
            use:content.elementText.use,
            func:() => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].elementText.use = !content.elementText.use;
            }))
        },
    ]

    const returnTable = () => {
        switch(category){
            case 0:
                // case 0  
                return(
                    <div>
                        <ElementsTable elements={elements} />
                        <LayoutRFG content={content} />
                        <EditTitleDesc content={content} />
                        <EditImageIcon content={content} />
                        <OpenCloseCustom title="" use={content.elementText.use}>
                            <ColorCustom text="" value={content.elementText.color} func={e => action.setContents(produce(state.contents, draft => {
                                draft[state.secNum].elementText.color = e;
                            }))} />
                            <RadioCustom text="" options={alignOptions} value={content.elementText.align} func={e => action.setContents(produce(state.contents, draft => {
                                draft[state.secNum].elementText.align = e;
                                draft[state.secNum].align = e;
                            }))} />
                            <OnOffCustom text=" " value={content.elementText.titleUse} func={(e) => action.setContents(produce(state.contents, draft => {
                                draft[state.secNum].elementText.titleUse = !content.elementText.titleUse;
                            }))} />
                            <TextSizeCustom text=" " elementTitle value={content.elementText.titleSize} func={e => action.setContents(produce(state.contents, draft => {
                                draft[state.secNum].elementText.titleSize = e;
                            }))} />
                            <OnOffCustom text=" " value={content.elementText.descUse} func={(e) => action.setContents(produce(state.contents, draft => {
                                draft[state.secNum].elementText.descUse = !content.elementText.descUse;
                            }))} />
                            <TextSizeCustom text=" " elementDesc value={content.elementText.descSize} func={e => action.setContents(produce(state.contents, draft => {
                                draft[state.secNum].elementText.descSize = e;
                            }))} />
                        </OpenCloseCustom>
                    </div>
                )

            case 1:
                // case 1  
                return(
                    <div>
                        <EditDesign content={content} />
                    </div>
                )

            default:
                <></>
        }
    }


    return(
        <>
            {returnTable()}
        </>
    )
}

export default EditFeaturesSection