import React, {useContext} from 'react'
import { MyContext } from '../../../../pages/Make/MakePageV2'
import EditDesign from './tools/EditDesign'
import produce from 'immer';
import ElementsTable from './tools/ElementsTable'
import EditTitleDesc from './tools/EditTitleDesc'
import RadioCustom from '../tools/Custom/RadioCustom'
import OnOffCustom from '../tools/Custom/OnOffCustom'
import ColorCustom from '../tools/Custom/ColorCustom'
import OpenCloseCustom from '../tools/Custom/OpenCloseCustom'
import TextSizeCustom from '../tools/func/TextSizeCustom'
import LayoutRFG from './tools/LayoutRFG'
import SliderCustom from '../tools/Custom/SliderCustom'

const layoutOptions = [
    { label: '1', value: 1},
    { label: '2', value: 2},
    { label: '3', value: 3},
    { label: '4', value: 4},
]
const shapeOptions = [
    { label: '', value: 0 },
    { label: '', value: 5 },
    { label: '', value: 20 },
]
const imageSizeOptions = [
    { label: '', value: 200 },
    { label: '', value: 250 },
    { label: '', value: 300 },
]
const alignOptions = [
    { label: '', value: 'start' },
    { label: '', value: 'center' },
]


function EditGallerySection({content, category}) {
    const {state, action} = useContext(MyContext) //ContextAPI state action .

    const elements = [
        {
            title:'',
            use:content.title.use,
            func:() => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].title.use = !content.title.use;
            }))
        },
        {
            title:'',
            use:content.desc.use,
            func:() => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].desc.use = !content.desc.use;
            }))
        },
        {
            title:'',
            use:content.element.use,
            func:() => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].element.use = !content.element.use;
            }))
        },
        {
            title:'',
            use:content.card.use,
            func:() => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].card.use = !content.card.use;
            }))
        },
    ]

    const returnTable = () => {
        switch(category){
            case 0:
                // case 0  
                return(
                    <div>
                        <ElementsTable elements={elements} />
                        <LayoutRFG content={content} />
                        <EditTitleDesc content={content} />
                        <OpenCloseCustom title="" use={content.element.use}>
                            <SliderCustom top="" value={content.element.size} func={e =>
                                action.setContents(produce(state.contents, draft => {
                                    draft[state.secNum].element.size = e
                                }))} max="500"/>
                        </OpenCloseCustom>
                        <OpenCloseCustom title='' use={content.card.use}>
                            <RadioCustom text="" value={content.card.borderRadius} options={shapeOptions} func={e => action.setContents(produce(state.contents, draft => {
                                    draft[state.secNum].card.borderRadius = e;
                                }))} />
                            <ColorCustom text=" " value={content.card.color} func={e => action.setContents(produce(state.contents, draft => {
                                draft[state.secNum].card.color = e;
                            }))} />
                            <OnOffCustom text=" " value={content.card.shadow} func={(e) => action.setContents(produce(state.contents, draft => {
                                draft[state.secNum].card.shadow = !content.card.shadow
                            }))} />
                            <ColorCustom text="  " value={content.text.color} func={e => action.setContents(produce(state.contents, draft => {
                                    draft[state.secNum].text.color = e;
                                }))} />
                        </OpenCloseCustom>
                    </div>
                )
            case 1:
                return(
                    <div>
                        <EditDesign content={content} />
                    </div>
                )
        }
    }

    return (
        <>
            {returnTable()}
        </>
    )
}

export default EditGallerySection
