import React, {useState, useContext} from 'react'
import { MyContext } from '../../../../../pages/Make/MakePageV2'
import RadioCustom from '../../tools/Custom/RadioCustom'
import produce from 'immer';
import ColorCustom from '../../tools/Custom/ColorCustom'
import TextSizeCustom from '../../tools/func/TextSizeCustom'
import OpenCloseCustom from '../../tools/Custom/OpenCloseCustom'

const alignOptions = [
    { label: '', value: 'start' },
    { label: '', value: 'center' },
]

function EditTitleDesc({content}) {
    const {state, action} = useContext(MyContext) //ContextAPI state action .

    return (
        <>
        <OpenCloseCustom title="" use={content.title.use}>
            <ColorCustom text="" value={content.title.color} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].title.color = e;
            }))} />
            <RadioCustom text="" options={alignOptions} value={content.title.align} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].title.align = e;
            }))} />
            <TextSizeCustom text="" title value={content.title.size} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].title.size = e;
            }))} />
        </OpenCloseCustom>
        <OpenCloseCustom title="" use={content.desc.use}>
            <ColorCustom text="" value={content.desc.color} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].desc.color = e;
            }))} />
            <RadioCustom text="" options={alignOptions} value={content.desc.align} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].desc.align = e;
            }))} />
            <TextSizeCustom text="" desc value={content.desc.size} func={e => action.setContents(produce(state.contents, draft => {
                draft[state.secNum].desc.size = e;
            }))} />
        </OpenCloseCustom>
        </>
    )
}

export default EditTitleDesc
